import React, { Component } from 'react';

// 
import './red.less';
import './blue.less';
import './dark.less';
import './magenta.less';

class ThemeProvider extends Component {

  render () {
    return (
      <div className={`${this.props.theme}-body`} id="body">
        { this.props.children }
      </div>
    )
  }
}

export default ThemeProvider



